.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
}
.modal .container {
  position: fixed;
  max-width: 600px;
  top: 15%;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 4em 2em;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.9);
}
.close {
  background-color: #f65835;
  border: 0;
  border-radius: 5px;
  position: absolute;
  top: 15px;
  left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 15px;
  color: #fff;
}
.close svg {
  margin-right: 5px;
}
.modal h2 {
  margin-bottom: 1.5em;
}
.modal span {
  font-weight: bold;
  font-size: 1.1em;
}
.modal span i {
  font-weight: 400;
  margin-right: 1em;
  padding: 2px 8px;
}
.row {
  margin-bottom: 1em;
}
.modal p {
  padding-top: 0.5em;
  white-space: pre-wrap;
  line-height: 150%;
}
.badge {
  padding: 3px;
  border-radius: 3px;
  color: #fff;
  margin-left: 5px;
}
